.home {
    padding: 0px 20px;
}
.carousel {
    margin-top: 65px;
}
.caption p{
    margin-bottom: 20%;
    font-family: nimbus-sans,Helvetica,Arial,sans-serif;
}
.title h3{
    padding-top: 80px;
    font-family: nimbus-sans,Helvetica,Arial,sans-serif;
    font-weight: 550;
    padding-right: 100px;
    padding-left: 100px;
}
@media screen and (max-width: 600px) {
    .home {
        padding: 0px 5px;
    }
}
@media screen and (max-width: 600px) {
    .carousel {
        margin-top: -25px;
        margin-bottom: 80px;
    }
}
@media screen and (max-width: 600px) {
    .caption p {
        margin-bottom: 10px;
    }
}
@media screen and (max-width: 600px) {
    .title h3 {
        padding-top: 30px;
        font-weight: 550;
        padding-right: 10px;
        padding-left: 10px;
        font-size: 20px;
    }
}
@media screen and (max-width: 600px) {
    .slider-control-bottomcenter {
        margin: 0 -10px;
    }
}